import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { UserdataService } from '../userdata.service';

@Component({
  selector: 'app-forms',
  templateUrl: './forms.component.html',
  styleUrls: ['./forms.component.css']
})
export class FormsComponent implements OnInit {

  constructor( private user: UserdataService, private route: Router ) { }

  ngOnInit(): void {
  }

  regform= new FormGroup({
    title: new FormControl("",[Validators.required,Validators.pattern("")]),
    snippet: new FormControl("",[Validators.required,Validators.pattern("")]),
    body: new FormControl("",[Validators.required,Validators.pattern("")])
  })

  submit() {
    this.user.postUser(this.regform.value).subscribe((res:any)=>{
      this.route.navigate(["/"]);
    });
  }

}
