import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { UserdataService } from '../userdata.service';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-putdetails',
  templateUrl: './putdetails.component.html',
  styleUrls: ['./putdetails.component.css']
})
export class PutdetailsComponent implements OnInit {

  constructor( public service: UserdataService, private route: Router, private activate: ActivatedRoute ) { }


  public newid:any;
  ngOnInit(): void {
    let id=this.activate.snapshot.paramMap.get("id");
    this.newid=id;
    console.log(this.service.obj._id);
    console.log(this.putform.value);
  }

  putform = new FormGroup({
    t: new FormControl(this.service.obj.Title,[Validators.required,Validators.pattern("")]),
    s: new FormControl(this.service.obj.Snippet,[Validators.required,Validators.pattern("")]),
    b: new FormControl(this.service.obj.Body,[Validators.required,Validators.pattern("")])
  })

  submit() {
    this.service.putUser(this.service.obj._id,this.putform.value).subscribe((res:any)=>{
      this.route.navigate(["/details",this.newid]);
    })
  }
}
