import { Component, OnInit } from '@angular/core';
import { UserdataService } from '../userdata.service';
import { ActivatedRoute, Router } from '@angular/router'

@Component({
  selector: 'app-details',
  templateUrl: './details.component.html',
  styleUrls: ['./details.component.css']
})
export class DetailsComponent implements OnInit {

  constructor( private activate: ActivatedRoute, public service: UserdataService, private route: Router ) { }

  public newid:any;
  public data:any=[];
  public data1:any={};
  public data2:any=[];

  ngOnInit(): void {
    let id=this.activate.snapshot.paramMap.get("id");
    this.newid=id;
    this.service.getUser().subscribe((res)=>{
      this.data=res;
      for(var i=0;i<this.data.length;i++) {
        if(this.data[i]._id==this.newid) {
          this.data1=this.data[i];
        }
      }
    })
  }

  delete(id:any) {
    this.service.deleteUser(id).subscribe((res)=>{
      let conf=confirm("Are You Sure!!");
      if(conf==true) {
        this.data2=res;
        setTimeout(()=>{
          this.route.navigate(["/"]);
        },500)
      } else {
        let currenturl=this.route.url;
        this.route.navigate([currenturl]);
      }
    })
  }

  put(id:any) {
    this.route.navigate(["putdetails",id]);
    this.service.sharedcomp(this.data1);
  }

  
}

